.container {
    background-image: url("../../img/Share_Image_1920x1080_1920x1080-3ec6b92b109d6ebe190c7fd8da23b989.png");
    height: 100%;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
  }
  
  .btn {
    background-color: red;
    color: black;
    border: 2px solid black;
    border-radius: 5px;
    width: 150px;
    height: 50px;
    font-size: large;
    font-weight: bold;
    top:20px;
  }
  
  .btn:hover {
    cursor: pointer;
    background-color: black;
    color: aqua;
    -webkit-box-shadow: 0px 0px 15px 10px aqua;
    box-shadow: 0px 0px 15px 10px aqua;
  }